import { notFound } from 'next/navigation';
import treatiesData from '../../../data/treaties.json';
import TaxLabUI from './TaxLabUI';
import { Metadata } from 'next';

interface Treaty {
  slug: string;
  source_country: string;
  destination_country: string;
  withholding_tax: number;
  residency_rule: string;
  treaty_article: string;
  currency: string;
  category: string;
}

const treaties = treatiesData as Treaty[];

export async function generateStaticParams() {
  return treaties.slice(0, 200).map((t) => ({
    slug: t.slug,
  }));
}

export async function generateMetadata(
  { params }: { params: Promise<{ slug: string }> }
): Promise<Metadata> {
  const resolvedParams = await params; // Menunggu params selesai di-load
  const data = treaties.find((t) => t.slug === resolvedParams.slug);
  
  if (!data) return { title: 'Not Found' };

  return {
    title: `Tax Treaty: ${data.source_country} to ${data.destination_country} | Luxury Tax Lab`,
    description: `Official 2026 tax simulation and treaty analysis for ${data.source_country} residents in ${data.destination_country}.`
  };
}

export default async function TaxPage(
  { params }: { params: Promise<{ slug: string }> }
) {
  const resolvedParams = await params; // Menunggu params selesai di-load
  const data = treaties.find((t) => t.slug === resolvedParams.slug);

  if (!data) notFound();

  // Mencari rekomendasi alternatif (SEO internal linking)
  const suggestions = treaties
    .filter((t) => t.slug !== resolvedParams.slug && t.source_country === data.source_country)
    .slice(0, 4);

  return <TaxLabUI data={data} suggestions={suggestions} />;
}